import { getEventList } from "@/api/modules/web/event";
import { Card, message } from "antd";
import Table, { ColumnsType } from "antd/es/table";
import { useEffect, useState } from "react";
import { useSearchParams } from "react-router-dom";


interface DataType {
    key: React.Key;
    name: string;
    metrics: string;
    categories: string;
}

const EventList = () => {
    const [pageNum, setPageNum] = useState(1);
    const [pageSize, setPageSize] = useState(10);
    const [eventTotal, setEventTotal] = useState(0);
    const [eventList, setEventList] = useState<DataType[]>([]);
    const [selectedRowKeys, setSelectedRowKeys] = useState<React.Key[]>([]);
    const [searchParams] = useSearchParams();
    const [messageApi, contextHolder] = message.useMessage();

    const columns: ColumnsType<DataType> = [
        {
            title: '事件名',
            dataIndex: 'name',
            width: 100,
        },
        {
            title: '自定义数值',
            dataIndex: 'metrics',
            width: 200,
        },
        {
            title: '事件本身的维度',
            dataIndex: 'categories',
            width: 200,
        },
    ];

    const onSelectChange = (newSelectedRowKeys: React.Key[]) => {
        setSelectedRowKeys(newSelectedRowKeys);
    };

    const rowSelection = {
        selectedRowKeys,
        onChange: onSelectChange,
    };

    const initEventList = async () => {
        const appId = searchParams.get('appId');
        if (!appId) {
            messageApi.open({
                type: 'error',
                content: 'appId不能为空!',
            });
            return;
        }
        const result = await getEventList({ pageNum, pageSize,appId });
        if (result.data) {
            setEventTotal(result.data.total);
            const data: DataType[] = [];
            result.data.data.forEach(item => {
                data.push({
                    key: item.eventId,
                    name: item.name,
                    metrics: item.metrics,
                    categories: item.categories
                })
            });
            setEventList(data);
        }
    };

    useEffect(() => {
        initEventList();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    useEffect(() => {
        initEventList();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [pageNum, pageSize])

    const pagination = {
        showSizeChanger: true,
        onChange: (page: number, pageSize: number) => {
            setPageNum(page);
            setPageSize(pageSize);
        },
        defaultCurrent: pageNum,
        total: eventTotal
    }

    return (<Card title="事件列表">{contextHolder}<Table style={{ marginTop: '12px' }} rowSelection={rowSelection} pagination={pagination} columns={columns} dataSource={eventList} /></Card>);
}

export default EventList;